    display: none; }
  .traffic {
    margin: 0 2.6%; }
    .traffic .row-1 {
      top: -10.25%; }
    .traffic .row-2 {
      top: 13%; }
      .traffic .row-2 .traffic__item:last-child {
        margin-right: 7.6%; }
    .traffic .row-3 {
      top: 34.6%; }
      .traffic .row-3 .traffic__item:first-child {
        margin-left: 7.61%; }
      .traffic .row-3 .traffic__item:last-child {
        margin-right: 7.65%; }
    .traffic__item {
      width: 35.8%; }
  .director img {
    width: 277px; }
  .benefits {
    margin-top: -415px; }
    .benefits__list {
      padding: 0 15px 0 25px; }
    .benefits__text {
      font-size: 14px;
      display: inline; }
    .benefits__item {
      max-width: 345px;
      padding-left: 115px;
      margin-bottom: 40px; }
    .benefits__item:nth-child(odd) {
      max-width: 345px; }
  .examples__item img {
    max-height: 120px; } }

@media (max-width: 660px) {
  .director__info {
    margin-top: 40px; }
  .director img {
    width: 188px; }
  .director h3.director__title {
    font-size: 24px; }
  .director p.director__contact,
  .director p.director__name {
    font-size: 14px; }
  .director p.director__name {
    margin: 10px 0; }
  .benefits {
    margin-top: -380px; }
    .benefits .cloud {
      display: none; }
    .benefits__container {
      padding-top: 450px; }
    .benefits .benefits__item,
    .benefits .benefits__item:nth-child(odd) {
      max-width: 410px; }
    .benefits__title {
      font-size: 18px; }
    .benefits__list {
      margin: 0 auto;
      width: fit-content;
      padding: 0 15px; }
    .benefits__item {
      margin-bottom: 65px;
      min-height: 115px; }
    .benefits__item:nth-child(odd) {
      align-self: flex-start; }
    .benefits__item::before {
      font-size: 150px; }
    .benefits__item:nth-child(4)::before {
      left: -5px; }
    .benefits__item:first-child br {
      display: none; }
    .benefits__item:first-child:before {
      left: 15px; } }

@media (max-width: 530px) {
  .advantages .advantages__truck img {
    width: 400px;
    margin: 0px -30px 0px; } }

@media (max-width: 480px) {
  .header .btn {
    display: none; }
  .advantages__list {
    flex-direction: column; }
  .advantages__column {
    display: flex; }
  .advantages__item {
    flex-direction: column;
    text-align: center;
    margin-bottom: 35px; }
  .advantages__text {
    padding-left: 0px;
    margin-top: 9px;
    max-width: 133px; }
  .advantages__icon {
    min-width: 53px;
    min-height: 53px; }
  .examples__item img {
    max-height: 90px; } }

.guarantees__container {
  max-width: 1214px;
  width: 100%;
  margin: 0 auto; }

.guarantees .section-title {
  margin-top: 45px; }

.guarantees .section-title::before, .guarantees .section-title::after {
  background-color: #C4C4C4; }

.guarantees .top-list__icon {
  width: 53px;
  height: 53px;
  background-color: #F1F1F1;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center; }

@media (max-width: 480px) {
  .advantages {
    padding-bottom: 80px; }
  .traffic__kinds-holder .traffic__row {
    flex-direction: column;
    align-items: center; }
    .traffic__kinds-holder .traffic__row .traffic__item:first-child {
      margin: 0 0 9.2% 0; }
    .traffic__kinds-holder .traffic__row .traffic__item:last-child {
      margin: 0; }
  .traffic .cloud {
    display: none; }
  .traffic__item {
    margin: 0;
    width: 57.8%; }
  .traffic__kinds-holder {
    margin-top: 100px; }
  .traffic__name {
    margin-top: 8%;
    font-size: 3vw; }
  .traffic .row-1 {
    top: -6.6%; }
  .traffic .row-2 {
    top: 21%; }
  .traffic .row-3 {
    top: 48.6%; }
    .traffic .row-3 .traffic__item:first-child {
      margin: 0 0 7.2% 0; }
  .traffic .row-4 {
    top: 76.1%; }
    .traffic .row-4 .traffic__item:first-child {
      width: 85%;
      margin-left: 0; }
  .traffic .row-5 {
    top: 95.4%; }
    .traffic .row-5 .traffic__item:first-child {
      width: 100%; } }

@media (max-width: 450px) {
  .director {
    position: relative; }
    .director img {
      width: 145px;
      position: absolute;
      right: 0;
      top: 100px; }
    .director__info {
      max-width: 272px; }
    .director p.director__name {
      margin: 40px 0 20px 0; }
  .benefits {
    margin-top: -270px; } }

@media (max-width: 360px) {
  .advantages .advantages__truck img {
    width: 350px;
    margin: 0px -30px 0px; }
  .advantages__list {
    flex-direction: row; }
  .advantages__column {
    flex-direction: column; }
  .traffic {
    padding-bottom: 7.7%; }
    .traffic__kinds-holder {
      margin-top: 45px; }
    .traffic__background {
      display: none; }
    .traffic__row {
      position: relative;
      top: 0 !important; }
    .traffic__item {
      width: 86%; }
    .traffic__name {
      font-size: 14px; }
    .traffic__item {
      margin-bottom: 40px !important; }
  .quality {
    margin-top: 0; }
  .director__info {
    margin-top: 0; }
  .benefits__list {
    margin: 0 17px 0 12px;
    padding: 0; }
  .benefits__item {
    max-width: 280px;
    min-height: 0px;
    padding-left: 30px !important;
    margin-bottom: 50px; }
    .benefits__item::before {
      font-size: 24px;
      top: 0px;
      opacity: 0.5; }
    .benefits__item:first-child::before {
      left: 3px; }
    .benefits__item:nth-child(4):before {
      left: -1px; }
  .benefits__estimate-btn {
    margin-top: 25px;
    padding-bottom: 70px; } }
