@import './base/normalize.scss';

@import './base/vars.scss';


@import './base/base.scss';


//SECTION HEADER

.header {


	position: absolute;
	width: 100%;
	height: 82px;
	font-size: 16px;
	z-index: 10;

	&__container {
		max-width: 1820px;
		padding: 0 10px;

		width: 100%;
		height: 100%;
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}

	&__link {
		margin-right: auto;
	}

	.nav {
		display: flex;
		align-items: center;
		margin-left: 40px;

		&__link {
			color: $main-white;
			font-weight: 600;
			transition: color 0.3s;

			&:hover {
				color: $main-blue;
			}
		}

		&__item {
			margin-right: 52px;
		}
	}

	&__info {
		display: flex;

		&-item {
			margin-right: 34px;
			padding-left: 32px;
			color: $main-white;
			white-space: nowrap;
		}

		.location {
			background-image: url('../images/icons/pin-icon.png');
			background-repeat: no-repeat;
			background-position-y: center;
		}

		.timetable {
			background: url('../images/icons/time-icon.png') no-repeat left center;
		}
	}

	&__feedback-btn {
		color: $main-white;
    font-weight: 600;
		border: 2px solid $main-white;
		padding: 4px 44px;
		transition: background-color 0.3s, border-color 0.3s;

			&:hover {
				background-color: $main-blue;
				border-color: $main-blue;
			}
	}
}

//SECTION INTRO

.intro {
	background: url('../images/intro.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 35%;
	height: 100vh;
	position: relative;

	&__info {
		position: absolute;
		top: 28%;

		max-width: 555px;
		width: 100%;
		z-index: 3;
	}

	&__title {
		font-weight: bold;
		font-size: 48px;
		line-height: 1.23;
		color: $main-white;
		padding-bottom: 10px;
		border-bottom: 2px solid $main-blue;
		margin-bottom: 37px;
	}

	&__text {
		font-weight: 500;
		font-size: 18px;
		line-height: 1.22;
		color: $main-white;
		margin-bottom: 30px;
	}

	&__btn {
		margin-top: 55px;
	}

	&__down-btn {
		display: flex;
    justify-content: center;

    position: absolute;
    left: 50%;
    bottom: 25px;
    z-index: 11;

		svg {
			fill: $main-white;
			transition: .3s;
		}

		&:hover svg {
			fill: $main-blue !important;
		}
	}
}

.intro::after {
	background-color: rgba(0,0,0,.4);
	height: 100%;
	position: absolute;
	width: 100%;
	top: 0;
	left: 0;
	content: '';
}


//SECTION ADVANTAGES

.advantages {
	background: linear-gradient(180deg, #CFD4DB 43.05%, rgba(186, 193, 201, 0) 100%);
	padding-bottom: 185px;

	.bg{
		display: flex;
		justify-content: flex-start;

		&__block {
			width: 187px;
			height: 15px;
			background-color: #003C5F;

			&:nth-child(2) {
				background-color: #0A6397;
			}

			&:nth-child(3) {
				background-color: #008DD2;;
			}
		}
	}

	&__info {
		display: flex;
		justify-content: center;
		margin: 0 15px;
		margin-top: 70px;
	}
	&__truck {
		display: flex;
		align-items: center;
	}

	&__truck img {
		margin-left: -145px; // 145 px - тень у картики слева

	}

	&__list {
		max-width: 350px;
		width: 100%;
		display: flex;
		flex-direction: column;
	}

	&__item {
		margin-bottom: 19px;

		display: flex;
		justify-content: flex-start;
		align-items: center;
	}

	&__icon {
		display: block;
		min-width: 56px;
		min-height: 56px;
		border-radius: 50%;
		border: 2px solid #008DD2;
		background-repeat: no-repeat;
		background-position: center;
	}

	&__text {

		font-size: 18px;
		line-height: 1.22;
		color: $main-black;
		padding-left: 47px;
	}
}

//SECTION TRAFFIC

.traffic {
	margin: 0 6%;

	margin-top: -40px; 
	padding-bottom: 12.5%;
	background-color: $main-white;

	.section-title {
		max-width: 1200px;
		margin: 0 auto;
	}

	.section-title::before,
	.section-title::after {
		background-color: #C4C4C4;
		max-width: 340px;
	}

	&__background {
		width: 100%;
	}

	&__content {
		position: relative;
	}

	.cloud {
		position: absolute;

		&--big {
			width: 21%;
		}

		&--small {
			width: 7%;
		}

		&--medium {
			width: 13%;
		}

		&-1 {
			top: -20%;
		}
		&-2 {
			top: -24%;
			right: 0%;

			@media (max-width: 1000px) {
				top: -15%;
			}
		}
		&-3 {
			top: 7%;
		}
		&-4 {
			top: 8%;
			left: 40%;
			width: 8%;
		}
		&-5 {
			top: 8%;
			right: 10%;
			transform: scale(-1, 1)
		}
		&-6 {
			top: 38%;
			left: 14%;
			width: 7%;
		}
		&-7 {
			top: 31%;
			left: 43%;
		}
		&-8 {
			top: 31%;
			right: 14%;
			width: 7%;
		}
		&-9 {
			top: 65%;
			left: 10%;
			width: 7%;
		}
		&-10 {
			top: 58%;
			left: 14%;
			transform: scale(-1, 1);
		}
		&-11 {
			top: 58%;
			right: 14%;
			transform: scale(-1, 1)
		}
		&-12 {
			top: 66%;
			right: 10%;
			width: 7%;
		}
		&-13 {
			top: 90%;
			left: 25%;
		}
		&-14 {
			top: 84%;
    	left: 43%;
			width: 7%;
		}
		&-15 {
			top: 86%;
    	right: 32%;
			width: 7%;
		}
	}

	&__kinds-holder {
		max-width: 1436px;
 		width: 100%;  
		margin: 0 auto;
		margin-top: 290px;
		position: relative;
	}

	&__row {
		position: absolute;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		width: 100%;
	}

	.row-1 {
		top: -10.2%;  

		.traffic__item:first-child {
			margin-left: 20.2%;
		}

		.traffic__item:last-child {
			margin-right: 11%;
		}
	}

	.row-2 {
		top: 13.2%;

		.traffic__item:first-child {
			margin-left: 8.4%;
		}

		.traffic__item:last-child {
			margin-right: 20.6%;
		}
	}

	.row-3 {
		top: 34.59%;

		.traffic__item:first-child {
			margin-left: 20.61%;
		}

		.traffic__item:last-child {
			margin-right: 10.65%;
		}
	}

	.row-4 {
		top: 57.3%;

		.traffic__item:first-child {
			margin-left: 24.3%;
			width: 47%;
		}
	}

	.row-5 {
		top: 91.4%;

		.traffic__item:first-child {
			margin-left: 17.77%;
			width: 64.6%;
		}
	}

	&__item {
		width: 30.6%; 
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__img {
		width: 100%; 
	}

	&__name {
		font-size: 18px;
		line-height: 1.22;
		text-transform: uppercase;
		font-weight: 600;
		margin-top: 21px;
	}
}



// section quality

.quality {
	margin-top: 140px;
	position: relative;
}

.director {

	display: flex;
	max-width: 1100px;
	width: 100%;
	margin: 0 auto;

	img {
		margin-left: -95px;
	}

	&__title {
		font-weight: 600;
		font-size: 50px;
		line-height: 1.06;
		color: #0A6397;
		max-width: 570px;
	}

	&__name {
		font-weight: bold;
		font-size: 20px;
		line-height: 1.2;
		margin: 25px 0;
	}

	&__info {
		margin-top: 78px;
		margin-left: 20px;
	}

	&__contact {
		max-width: 247px;
		font-size: 18px;
		line-height: 1.22;
	}
}

.benefits {
	background: url("../images/benefits-bg.png") no-repeat;
	margin-top: -455px;

	&__top-container {
		position: relative;
	}

	&__container {
		max-width: 1100px;
		width: 100%;
		margin: 0 auto;
		padding-top: 285px;
	}

	&__list {
		display: flex;
		flex-direction: column;
		list-style-type: none; 
		counter-reset: num;
	}

	&__item {
		color: $main-white;
		max-width: 475px;
		padding-left: 146px;
		width: 100%;
		margin-bottom: 90px;
		position: relative;
		
	}

	&__item::before {
		content: counter(num);
		counter-increment: num;
		font-weight: 300;
		font-size: 170px;
		position: absolute;
		line-height: 1;
		top: -18px;
		left: 0px;
		color: #0A6397;

		text-shadow: 
			-0   -1px 1px #FFFFFF,
			0   -1px 1px #FFFFFF,
			-0    1px 1px #FFFFFF,
			0    1px 1px #FFFFFF,
			-1px -0   1px #FFFFFF,
			1px -0   1px #FFFFFF,
			-1px  0   1px #FFFFFF,
			1px  0   1px #FFFFFF,
			-1px -1px 1px #FFFFFF,
			1px -1px 1px #FFFFFF,
			-1px  1px 1px #FFFFFF,
			1px  1px 1px #FFFFFF,
			-1px -1px 1px #FFFFFF,
			1px -1px 1px #FFFFFF,
			-1px  1px 1px #FFFFFF,
			1px  1px 1px #FFFFFF;
	}

	&__item:nth-child(odd) {
		justify-self: flex-end;
		align-self: flex-end;
		max-width: 538px;
	}

	&__item:first-child::before {
		left: 25px;
	}

	&__title {
		font-weight: bold;
		font-size: 20px;
		margin-bottom: 15px;
	}

	&__text {
		font-size: 18px;
		line-height: 125.9%;
	}

	&__estimate-btn {
		text-align: center;
		margin-top: 50px;
		padding-bottom: 100px;

		a {
			font-size: 14px;
			text-align: center;
			color: $main-white;
			display: inline-block;
			padding: 12px 41px;
			background-color: #0093FF;
			margin: 0 auto;
		}
	}

	.cloud {
		position: absolute;

		&--big {
			width: 21%;
		}

		&--small {
			width: 7%;
		}

		&--medium {
			width: 13%;
		}

		&-1 {
			top: 28%;
    	left: 5%;

			@media (max-width: 1000px) {
				top: 31%;
			}
		}
		&-2 {
			top: 30%;
			right: 5%;
			
			@media (max-width: 768px) {
				top: 41%;
			}
		}
		&-3 {
			top: 46%;
    	left: 5%;
			@media (max-width: 768px) {
				top: 53%;
    		left: 15%;
			}
		}
		&-4 {
			top: 43%;
    	left: 40%;
			width: 8%;
			@media (max-width: 768px) {
				display: none;
			}
		}
		&-5 {
			top: 60%;
			right: 10%;
			transform: scale(-1, 1)
		}
		&-6 {
			top: 70%;
			left: 14%;
			width: 7%;
			@media (max-width: 768px) {
				display: none;
			}
		}
		&-7 {
			top: 78%;
    	left: 25%;

			@media (max-width: 1000px) {
				left: 10%;
			}
		}
		&-8 {
			top: 31%;
			right: 14%;
			width: 7%;
		}
		&-9 {
			top: 65%;
			left: 10%;
			width: 7%;
		}
		&-10 {
			top: 58%;
			left: 14%;
			transform: scale(-1, 1);
		}
	}
}


// examples section

.examples {
	&__item {
		max-height: 177px;
		width: auto;
		cursor: pointer;
	}


}









