		p.director__name {
			margin: 40px 0 20px 0;
		}
	}

	.benefits {
    margin-top: -270px;
	}
}

@media (max-width: 360px) {
	.advantages .advantages__truck img {
		width: 350px;
		margin: 0px -30px 0px;
	}

	.advantages {
		&__list {
			flex-direction: row;
		}

		&__column {
			flex-direction: column;
		}
	}

	.traffic {
		padding-bottom: 7.7%;

		&__kinds-holder {
			margin-top: 45px;
		}

		&__background {
			display: none;
		}

		&__row {
			position: relative;
			top: 0 !important;
		}

		&__item {
			width: 86%;
		}

		&__name {
			font-size: 14px
		}

		&__item {
			margin-bottom: 40px !important; 
		}
	}

	.quality {
		margin-top: 0;
	}

	.director {
		&__info {
