		}
	}

	.benefits {

		&__list {
			margin: 0 17px 0 12px;
			padding: 0;
		}

		&__item {
			
			max-width: 280px;
			min-height: 0px;
			padding-left: 30px !important;
			margin-bottom: 50px;


			&::before {
				font-size: 24px;
				top: 0px;
